.backdrop {
  position: fixed;
  z-index: 1000;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.2);

  transition: var(time-animation);

  padding: 94px 15px;
  overflow-y: scroll;
  margin: auto;
}

.modal {
  position: relative;
  padding: 40px;

  background: var(--secondary-background-color);
  background-position: center;
  box-shadow: 0px 1px 3px rgba(0, 0, 0, 0.12), 0px 1px 1px rgba(0, 0, 0, 0.14),
    0px 2px 1px rgba(0, 0, 0, 0.2);
  border-radius: 4px;

  @media screen and (min-width: 320px) {
    width: 100%;
  }

  @media screen and (min-width: 480px) and (max-width: 1199px) {
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    max-width: 450px;
    height: 609px;
  }

  @media screen and (min-width: 1200px) {
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);

    width: 528px;
    height: 581px;
  }
}

.modal-close {
  display: flex;
  position: absolute;

  right: 8px;
  top: 8px;
  padding: 0;
  margin: 0;
  width: 30px;
  height: 30px;

  border: 0.5px solid rgba(0, 0, 0, 0.1);
  border-radius: 50%;
  background-color: transparent;

  fill: rgba(175, 177, 184, 1);
  cursor: pointer;

  background-position: center;

  justify-content: center;
  align-items: center;
  transition: var(--time-animation);

  @media screen and (min-width: 767px) {
    top: 8px;
    right: 8px;
  }
}

.icon-close {
  width: 18px;
  height: 18px;

  .modal-close:hover &,
  .modal-close:focus & {
    fill: var(--button-hover-primary);
  }
}

.is-hidden {
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
}
